@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@100;300;400;500;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.initiator {
  display: none;
}

.player {
  display: none;
}

.get-in-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


@media only screen and (max-width: 768px) {
  .player {
    display: flex;
  }
}

@media only screen and (min-width: 769px) {
  .initiator {
    display: flex;
  }
}



.App {
  min-height: 100vh;
  padding: 0 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Heebo', sans-serif;
}

.player__input {
  width: 100%;
  border: 4px solid black;
  padding: 20px;
  font-size: 36px;
  margin: 60px;
  font-family: 'Heebo', sans-serif;
  font-weight: 800;
  text-align: center;
  box-sizing: border-box;
}

.player__button {
  width: 100%;
  padding: 20px;
  font-size: 36px;
  background-color: black;
  color: white;
  font-family: 'Heebo', sans-serif;
}

main {
  padding: 5rem 0;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  margin: 0;
  line-height: 1.15;
  font-size: 3rem;
}

.title,
.description {
  text-align: center;
}

.description {
  line-height: 1.5;
  font-size: 1.5rem;
}

.participants {
  line-height: 1.5;
  font-size: 1.5rem;
  list-style:none;
  margin:0;
  padding:0;
}

